import React, { Component } from 'react';
import styles from "./About.scss"
import { Parallax } from 'react-scroll-parallax';

class About extends Component {
  render() {
    return (
        <div className={styles.about} id={this.props.id}>
            <div className="container">
                <div className="row">
                    <div className={styles.imagewrapper}>
                        <img src="http://placehold.it/500x600/000/fff?width=500" className={styles.image} alt="Placeholder" />
                    </div>
                    <div className={styles.contentwrapper}>
                    <div className={styles.content}>
                    <Parallax
        offsetYMax={100}
        offsetYMin={-100}
        tag="div"
    >

                        <h2 className="heading">About</h2>
                        <p>All round web developer speialising in front end with industry experience of coding tools and technologies including, HTML 5, CSS 3, JavaScript, React, Redux, jQuery, Bootstrap &amp; responsive web techniques.</p>
                        </Parallax>
                        </div>

                    </div>

                </div>
            </div>
        </div>
    );
  }
}

export default About;


